/**
 * ÊèêÁèæÈ†ÅÈù¢
 * Wallet Withdraw View
 * 
 * Áî®Êà∂ÊèêÁèæÂäüËÉΩÔºö
 * - ÊèêÁèæÁî≥Ë´ã
 * - ÊèêÁèæË®òÈåÑ
 * - ÊâãÁ∫åË≤ªË®àÁÆó
 */

import { Component, OnInit, signal, computed } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Router } from '@angular/router';
import { WalletService } from '../services/wallet.service';
import { ApiService } from '../core/api.service';

interface WithdrawConfig {
  min_amount: number;
  max_amount: number;
  daily_max: number;
  fee_rate: number;
  min_fee: number;
  free_monthly_count: number;
  methods: { id: string; name: string; enabled: boolean }[];
}

interface WithdrawOrder {
  order_no: string;
  amount: number;
  amount_display: string;
  fee: number;
  fee_display: string;
  actual_amount: number;
  actual_display: string;
  method: string;
  address: string;
  status: string;
  tx_hash?: string;
  created_at: string;
  completed_at?: string;
}

@Component({
  selector: 'app-wallet-withdraw',
  standalone: true,
  imports: [CommonModule, FormsModule],
  template: `
    <div class="withdraw-view">
      <!-- È†≠ÈÉ® -->
      <div class="view-header">
        <div class="header-left">
          <button class="back-btn" (click)="goBack()">‚Üê</button>
          <h1>ÊèêÁèæ</h1>
        </div>
        <button class="history-btn" (click)="showHistory()">üìã ÊèêÁèæË®òÈåÑ</button>
      </div>

      <!-- ÂèØÊèêÁèæÈ§òÈ°ç -->
      <div class="balance-card">
        <div class="balance-label">ÂèØÊèêÁèæÈ§òÈ°ç</div>
        <div class="balance-amount">{{ walletService.balanceDisplay() }}</div>
        <div class="balance-note">Ë¥àÈÄÅÈ§òÈ°ç‰∏çÂèØÊèêÁèæ</div>
      </div>

      <!-- ÊèêÁèæË°®ÂñÆ -->
      <div class="section">
        <h2>ÊèêÁèæÈáëÈ°ç</h2>
        <div class="amount-input">
          <span class="currency">$</span>
          <input 
            type="number" 
            [(ngModel)]="withdrawAmount"
            [min]="(config()?.min_amount || 0) / 100"
            [max]="(config()?.max_amount || 0) / 100"
            placeholder="Ë´ãËº∏ÂÖ•ÊèêÁèæÈáëÈ°ç"
            (ngModelChange)="onAmountChange()"
          />
        </div>
        <div class="amount-hints">
          <span>ÊúÄ‰Ωé {{ formatAmount(config()?.min_amount || 1000) }}</span>
          <span>|</span>
          <span>ÂñÆÁ≠Ü‰∏äÈôê {{ formatAmount(config()?.max_amount || 100000) }}</span>
        </div>
        <div class="quick-amounts">
          <button 
            *ngFor="let amt of quickAmounts"
            class="quick-btn"
            [class.active]="withdrawAmount === amt"
            (click)="setAmount(amt)"
          >
            {{ formatAmount(amt * 100) }}
          </button>
          <button 
            class="quick-btn all"
            (click)="setMaxAmount()"
          >
            ÂÖ®ÈÉ®ÊèêÁèæ
          </button>
        </div>
      </div>

      <!-- ÊèêÁèæÊñπÂºè -->
      <div class="section">
        <h2>ÊèêÁèæÊñπÂºè</h2>
        <div class="methods">
          <div 
            *ngFor="let method of config()?.methods || []"
            class="method-item"
            [class.selected]="selectedMethod === method.id"
            [class.disabled]="!method.enabled"
            (click)="method.enabled && selectMethod(method.id)"
          >
            <span class="method-icon">{{ getMethodIcon(method.id) }}</span>
            <div class="method-info">
              <span class="method-name">{{ method.name }}</span>
              <span class="method-status" *ngIf="!method.enabled">Âç≥Â∞á‰∏äÁ∑ö</span>
            </div>
            <span class="check" *ngIf="selectedMethod === method.id">‚úì</span>
          </div>
        </div>
      </div>

      <!-- ÊèêÁèæÂú∞ÂùÄ -->
      <div class="section" *ngIf="selectedMethod">
        <h2>{{ getMethodLabel(selectedMethod) }}</h2>
        <input 
          type="text" 
          class="address-input"
          [(ngModel)]="withdrawAddress"
          [placeholder]="getAddressPlaceholder(selectedMethod)"
        />
        <div class="address-hint">Ë´ãÁ¢∫‰øùÂú∞ÂùÄÊ≠£Á¢∫ÔºåÊèêÁèæÂæåÁÑ°Ê≥ïÊí§Âõû</div>
      </div>

      <!-- ÊèêÁèæÊòéÁ¥∞ -->
      <div class="section summary">
        <h2>ÊèêÁèæÊòéÁ¥∞</h2>
        <div class="summary-row">
          <span class="label">ÊèêÁèæÈáëÈ°ç</span>
          <span class="value">{{ formatAmount(withdrawAmountCents()) }}</span>
        </div>
        <div class="summary-row">
          <span class="label">ÊâãÁ∫åË≤ª ({{ (config()?.fee_rate || 0) * 100 }}%)</span>
          <span class="value fee">-{{ formatAmount(feeAmount()) }}</span>
        </div>
        <div class="summary-row total">
          <span class="label">ÂØ¶ÈöõÂà∞Ë≥¨</span>
          <span class="value">{{ formatAmount(actualAmount()) }}</span>
        </div>
        <div class="free-note" *ngIf="isFreeWithdraw()">
          üéâ Êú¨ÊúàÂÖçË≤ªÊèêÁèæÊ©üÊúÉÔºåÊâãÁ∫åË≤ª $0
        </div>
      </div>

      <!-- Êèê‰∫§ÊåâÈàï -->
      <div class="action-bar">
        <button 
          class="submit-btn"
          [disabled]="!canSubmit()"
          (click)="submit()"
        >
          {{ loading() ? 'ËôïÁêÜ‰∏≠...' : 'Á¢∫Ë™çÊèêÁèæ' }}
        </button>
      </div>

      <!-- ÊèêÁèæË®òÈåÑÂΩàÁ™ó -->
      <div class="modal-overlay" *ngIf="showHistoryModal()" (click)="closeHistory()">
        <div class="modal" (click)="$event.stopPropagation()">
          <div class="modal-header">
            <h3>ÊèêÁèæË®òÈåÑ</h3>
            <button class="close-btn" (click)="closeHistory()">√ó</button>
          </div>
          <div class="modal-body">
            <div class="order-list">
              <div class="order-item" *ngFor="let order of withdrawOrders()">
                <div class="order-main">
                  <div class="order-info">
                    <div class="order-no">{{ order.order_no }}</div>
                    <div class="order-time">{{ formatDate(order.created_at) }}</div>
                  </div>
                  <div class="order-amount">
                    <div class="amount">{{ order.actual_display }}</div>
                    <div class="status" [style.color]="getStatusColor(order.status)">
                      {{ getStatusName(order.status) }}
                    </div>
                  </div>
                </div>
                <div class="order-detail">
                  <span>{{ order.method }} ‚Üí {{ maskAddress(order.address) }}</span>
                </div>
              </div>
              <div class="empty" *ngIf="withdrawOrders().length === 0">
                Êö´ÁÑ°ÊèêÁèæË®òÈåÑ
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .withdraw-view {
      min-height: 100vh;
      background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
      padding: 20px;
      padding-bottom: 100px;
      color: #fff;
    }

    .view-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px;
    }

    .header-left {
      display: flex;
      align-items: center;
      gap: 12px;
    }

    .back-btn {
      width: 40px;
      height: 40px;
      border-radius: 12px;
      background: rgba(255, 255, 255, 0.1);
      border: none;
      color: #fff;
      font-size: 20px;
      cursor: pointer;
    }

    h1 {
      font-size: 24px;
      font-weight: 600;
      margin: 0;
    }

    .history-btn {
      padding: 10px 16px;
      background: rgba(255, 255, 255, 0.1);
      border: none;
      border-radius: 10px;
      color: #fff;
      font-size: 14px;
      cursor: pointer;
    }

    /* È§òÈ°çÂç°Áâá */
    .balance-card {
      background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      border-radius: 20px;
      padding: 24px;
      text-align: center;
      margin-bottom: 24px;
    }

    .balance-label {
      font-size: 14px;
      opacity: 0.8;
    }

    .balance-amount {
      font-size: 36px;
      font-weight: 700;
      margin: 8px 0;
    }

    .balance-note {
      font-size: 12px;
      opacity: 0.6;
    }

    /* ÂçÄÂ°ä */
    .section {
      background: rgba(255, 255, 255, 0.05);
      border-radius: 16px;
      padding: 20px;
      margin-bottom: 20px;
    }

    .section h2 {
      font-size: 16px;
      font-weight: 600;
      margin: 0 0 16px 0;
    }

    /* ÈáëÈ°çËº∏ÂÖ• */
    .amount-input {
      display: flex;
      align-items: center;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 12px;
      padding: 0 16px;
    }

    .amount-input .currency {
      font-size: 24px;
      font-weight: 600;
      opacity: 0.6;
    }

    .amount-input input {
      flex: 1;
      padding: 16px 12px;
      background: transparent;
      border: none;
      color: #fff;
      font-size: 24px;
      font-weight: 600;
    }

    .amount-input input::placeholder {
      color: rgba(255, 255, 255, 0.3);
    }

    .amount-hints {
      display: flex;
      justify-content: center;
      gap: 12px;
      margin-top: 12px;
      font-size: 12px;
      opacity: 0.6;
    }

    .quick-amounts {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      margin-top: 16px;
    }

    .quick-btn {
      padding: 10px 16px;
      background: rgba(255, 255, 255, 0.05);
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 8px;
      color: #fff;
      font-size: 14px;
      cursor: pointer;
    }

    .quick-btn.active {
      background: linear-gradient(135deg, #667eea, #764ba2);
      border-color: transparent;
    }

    .quick-btn.all {
      flex: 1;
      min-width: 100px;
    }

    /* ÊèêÁèæÊñπÂºè */
    .methods {
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    .method-item {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 16px;
      background: rgba(255, 255, 255, 0.05);
      border: 2px solid transparent;
      border-radius: 12px;
      cursor: pointer;
      transition: all 0.2s;
    }

    .method-item:hover:not(.disabled) {
      border-color: rgba(255, 255, 255, 0.2);
    }

    .method-item.selected {
      border-color: #667eea;
      background: rgba(102, 126, 234, 0.1);
    }

    .method-item.disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    .method-icon {
      font-size: 24px;
    }

    .method-info {
      flex: 1;
      display: flex;
      flex-direction: column;
    }

    .method-name {
      font-size: 15px;
      font-weight: 500;
    }

    .method-status {
      font-size: 12px;
      opacity: 0.6;
    }

    .check {
      color: #667eea;
      font-size: 18px;
    }

    /* Âú∞ÂùÄËº∏ÂÖ• */
    .address-input {
      width: 100%;
      padding: 14px 16px;
      background: rgba(255, 255, 255, 0.1);
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 12px;
      color: #fff;
      font-size: 14px;
      font-family: monospace;
    }

    .address-input:focus {
      outline: none;
      border-color: #667eea;
    }

    .address-hint {
      font-size: 12px;
      opacity: 0.5;
      margin-top: 8px;
    }

    /* ÊèêÁèæÊòéÁ¥∞ */
    .summary .summary-row {
      display: flex;
      justify-content: space-between;
      padding: 12px 0;
      border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    }

    .summary-row .label {
      opacity: 0.7;
    }

    .summary-row .value.fee {
      color: #ef4444;
    }

    .summary-row.total {
      padding-top: 16px;
      font-size: 18px;
      font-weight: 600;
      border-bottom: none;
    }

    .summary-row.total .value {
      color: #22c55e;
    }

    .free-note {
      text-align: center;
      padding: 12px;
      background: rgba(34, 197, 94, 0.1);
      border-radius: 8px;
      color: #22c55e;
      font-size: 13px;
      margin-top: 12px;
    }

    /* Êèê‰∫§ÊåâÈàï */
    .action-bar {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 16px 20px;
      background: rgba(26, 26, 46, 0.95);
      backdrop-filter: blur(10px);
    }

    .submit-btn {
      width: 100%;
      padding: 16px;
      background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      border: none;
      border-radius: 12px;
      color: #fff;
      font-size: 16px;
      font-weight: 600;
      cursor: pointer;
    }

    .submit-btn:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    /* ÂΩàÁ™ó */
    .modal-overlay {
      position: fixed;
      inset: 0;
      background: rgba(0, 0, 0, 0.7);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 100;
      padding: 20px;
    }

    .modal {
      background: #1a1a2e;
      border-radius: 20px;
      max-width: 400px;
      width: 100%;
      max-height: 70vh;
      overflow: hidden;
    }

    .modal-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 20px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    }

    .modal-header h3 {
      margin: 0;
      font-size: 18px;
    }

    .close-btn {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.1);
      border: none;
      color: #fff;
      font-size: 20px;
      cursor: pointer;
    }

    .modal-body {
      padding: 20px;
      max-height: 50vh;
      overflow-y: auto;
    }

    .order-list {
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    .order-item {
      padding: 16px;
      background: rgba(255, 255, 255, 0.05);
      border-radius: 12px;
    }

    .order-main {
      display: flex;
      justify-content: space-between;
      margin-bottom: 8px;
    }

    .order-no {
      font-family: monospace;
      font-size: 13px;
    }

    .order-time {
      font-size: 12px;
      opacity: 0.5;
    }

    .order-amount .amount {
      font-size: 16px;
      font-weight: 600;
      text-align: right;
    }

    .order-amount .status {
      font-size: 12px;
      text-align: right;
    }

    .order-detail {
      font-size: 12px;
      opacity: 0.6;
    }

    .empty {
      text-align: center;
      padding: 40px;
      opacity: 0.5;
    }
  `]
})
export class WalletWithdrawComponent implements OnInit {
  withdrawAmount = 0;
  selectedMethod = 'usdt_trc20';
  withdrawAddress = '';
  
  quickAmounts = [10, 50, 100, 500];
  
  config = signal<WithdrawConfig | null>(null);
  loading = signal(false);
  showHistoryModal = signal(false);
  withdrawOrders = signal<WithdrawOrder[]>([]);
  
  constructor(
    public walletService: WalletService,
    private api: ApiService,
    private router: Router
  ) {}
  
  ngOnInit() {
    this.loadConfig();
    this.walletService.loadWallet();
  }
  
  async loadConfig() {
    try {
      const response = await this.api.get<any>('/api/wallet/withdraw/config');
      if (response?.success && response?.data) {
        this.config.set(response.data);
      }
    } catch (error) {
      console.error('Load withdraw config error:', error);
    }
  }
  
  withdrawAmountCents(): number {
    return (this.withdrawAmount || 0) * 100;
  }
  
  feeAmount = computed(() => {
    const cfg = this.config();
    if (!cfg) return 0;
    
    const amount = this.withdrawAmountCents();
    if (amount <= 0) return 0;
    
    // TODO: Ê™¢Êü•ÊòØÂê¶ÈÇÑÊúâÂÖçË≤ªÊ¨°Êï∏
    const fee = Math.round(amount * cfg.fee_rate);
    return Math.max(fee, cfg.min_fee);
  });
  
  actualAmount = computed(() => {
    return this.withdrawAmountCents() - this.feeAmount();
  });
  
  isFreeWithdraw(): boolean {
    // TODO: ÂæûÊúçÂãôÂô®Áç≤ÂèñÊú¨ÊúàÂÖçË≤ªÊ¨°Êï∏
    return false;
  }
  
  canSubmit(): boolean {
    const cfg = this.config();
    if (!cfg) return false;
    if (this.loading()) return false;
    
    const amount = this.withdrawAmountCents();
    if (amount < cfg.min_amount) return false;
    if (amount > cfg.max_amount) return false;
    if (!this.selectedMethod) return false;
    if (!this.withdrawAddress) return false;
    
    return true;
  }
  
  onAmountChange() {
    // Ëß∏ÁôºË®àÁÆóÊõ¥Êñ∞
  }
  
  setAmount(amount: number) {
    this.withdrawAmount = amount;
  }
  
  setMaxAmount() {
    const wallet = this.walletService.wallet();
    if (wallet) {
      const cfg = this.config();
      const maxWithdraw = Math.min(wallet.balance, cfg?.max_amount || 100000);
      this.withdrawAmount = maxWithdraw / 100;
    }
  }
  
  selectMethod(method: string) {
    this.selectedMethod = method;
    this.withdrawAddress = '';
  }
  
  formatAmount(cents: number): string {
    return '$' + (cents / 100).toFixed(2);
  }
  
  formatDate(dateStr: string): string {
    if (!dateStr) return '';
    const date = new Date(dateStr);
    return date.toLocaleString('zh-TW', {
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    });
  }
  
  getMethodIcon(method: string): string {
    const icons: Record<string, string> = {
      'usdt_trc20': 'üíé',
      'usdt_erc20': 'üíé',
      'bank': 'üè¶'
    };
    return icons[method] || 'üí≥';
  }
  
  getMethodLabel(method: string): string {
    const labels: Record<string, string> = {
      'usdt_trc20': 'USDT Êî∂Ê¨æÂú∞ÂùÄ (TRC20)',
      'usdt_erc20': 'USDT Êî∂Ê¨æÂú∞ÂùÄ (ERC20)',
      'bank': 'ÈäÄË°åÂç°Ëôü'
    };
    return labels[method] || 'Êî∂Ê¨æÂú∞ÂùÄ';
  }
  
  getAddressPlaceholder(method: string): string {
    const placeholders: Record<string, string> = {
      'usdt_trc20': 'Ë´ãËº∏ÂÖ• TRC20 Âú∞ÂùÄÔºå‰ª• T ÈñãÈ†≠',
      'usdt_erc20': 'Ë´ãËº∏ÂÖ• ERC20 Âú∞ÂùÄÔºå‰ª• 0x ÈñãÈ†≠',
      'bank': 'Ë´ãËº∏ÂÖ•ÈäÄË°åÂç°Ëôü'
    };
    return placeholders[method] || 'Ë´ãËº∏ÂÖ•Êî∂Ê¨æÂú∞ÂùÄ';
  }
  
  getStatusName(status: string): string {
    const names: Record<string, string> = {
      'pending': 'ÂæÖÂØ©Ê†∏',
      'approved': 'Â∑≤ÊâπÂáÜ',
      'processing': 'ËôïÁêÜ‰∏≠',
      'completed': 'Â∑≤ÂÆåÊàê',
      'rejected': 'Â∑≤ÊãíÁµï',
      'cancelled': 'Â∑≤ÂèñÊ∂à'
    };
    return names[status] || status;
  }
  
  getStatusColor(status: string): string {
    const colors: Record<string, string> = {
      'pending': '#f59e0b',
      'approved': '#3b82f6',
      'processing': '#8b5cf6',
      'completed': '#22c55e',
      'rejected': '#ef4444',
      'cancelled': '#9ca3af'
    };
    return colors[status] || '#666';
  }
  
  maskAddress(address: string): string {
    if (!address || address.length < 10) return address;
    return address.slice(0, 6) + '...' + address.slice(-4);
  }
  
  async submit() {
    if (!this.canSubmit()) return;
    
    this.loading.set(true);
    
    try {
      const response = await this.api.post<any>('/api/wallet/withdraw/create', {
        amount: this.withdrawAmountCents(),
        method: this.selectedMethod,
        address: this.withdrawAddress
      });
      
      if (response?.success) {
        alert('ÊèêÁèæÁî≥Ë´ãÂ∑≤Êèê‰∫§ÔºåË´ãÁ≠âÂæÖÂØ©Ê†∏');
        await this.walletService.loadWallet();
        this.router.navigate(['/wallet']);
      } else {
        alert(response?.error || 'ÊèêÁèæÂ§±Êïó');
      }
    } catch (error) {
      console.error('Submit withdraw error:', error);
      alert('ÊèêÁèæÂ§±Êïó');
    } finally {
      this.loading.set(false);
    }
  }
  
  async showHistory() {
    try {
      const response = await this.api.get<any>('/api/wallet/withdraw/orders');
      if (response?.success && response?.data?.orders) {
        this.withdrawOrders.set(response.data.orders);
      }
    } catch (error) {
      console.error('Load withdraw orders error:', error);
    }
    
    this.showHistoryModal.set(true);
  }
  
  closeHistory() {
    this.showHistoryModal.set(false);
  }
  
  goBack() {
    this.router.navigate(['/wallet']);
  }
}
